
.font-color-base {
  color: #333333;
}
.font-color-gray {
  color: #7D7D7D;
}
.font-color-red {
  color: #E94543;
}
.font-color-blue {
  color: #42AEF6;
}
.font-color-orange {
  color: #FF820F;
}
.font-color-white {
  color: #FFFFFF;
}
.font-size-base {
  font-size: 0.28rem;
}
.font-size-medium {
  font-size: 0.3192rem;
}
.font-size-large {
  font-size: 0.3388rem;
}
.font-size-larger {
  font-size: 0.3612rem;
}
.font-size-largest {
  font-size: 0.392rem;
}
.font-size-small {
  font-size: 0.308rem;
}
.font-size-smaller {
  font-size: 0.2604rem;
}
.font-size-smallest {
  font-size: 0.2408rem;
}
.font-size-big {
  font-size: 0.448rem;
}
.font-size-bigger {
  font-size: 0.504rem;
}
.font-size-biggest {
  font-size: 0.5992rem;
}
.font-size-very {
  font-size: 0.728rem;
}
.font-size-max {
  font-size: 0.784rem;
}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.payment-amount {
  padding-left: .3rem;
}
.payment-amount__title {
  margin-top: .4rem;
  margin-bottom: .12rem;
}
.payment-amount__value {
  font-size: 0.504rem;
  margin-top: 0;
  margin-bottom: .45rem;
}
.payment-bank {
  padding: .3rem;
  width: 100%;
  position: relative;
}
.payment-bank__icon {
  display: inline-block;
  margin-right: .36rem;
  background: transparent;
  width: .7rem;
  height: .7rem;
  text-align: center;
}
.payment-bank__name {
  display: inline-block;
}
.payment-bank__name .bank-name {
  font-weight: bold;
}
.payment-bank__name .bank-name.disable {
  color: #7D7D7D;
}
.payment-bank__name .bank-limit {
  color: #7D7D7D;
}
.payment-bank__name p {
  margin: 0;
}
.payment-bank .radio-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  padding: 0;
}
.payment-bank .radio-icon input {
  visibility: hidden;
  position: absolute;
  right: .32rem;
  top: 40%;
}
.payment-bank .radio-icon input:checked + img {
  display: block;
}
.payment-bank .radio-icon img {
  width: .35rem;
  position: absolute;
  display: none;
  right: .32rem;
  top: 40%;
}
.balance-icon {
  display: inline-block;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 32px;
  height: 32px;
}
.balance-icon.enable {
  background-image: url(../../../images/puyipay/balance-icon-enable.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.balance-icon.disable {
  background-image: url(../../../images/puyipay/balance-icon-disable.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}



