  font-size: 0.2604rem;
}
.font-size-smallest {
  font-size: 0.2408rem;
}
.font-size-big {
  font-size: 0.448rem;
}
.font-size-bigger {
  font-size: 0.504rem;
}
.font-size-biggest {
  font-size: 0.5992rem;
}
.font-size-very {
  font-size: 0.728rem;
}
.font-size-max {
  font-size: 0.784rem;
}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.ranking-header {
  text-align: center;
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  max-width: 1080px;
}
.ranking-header_placeholder {
  width: 100%;
  height: 1.036rem;
}
.ranking-header_count {
  text-align: right;
  padding-right: .28rem;
  padding-top: .14rem;
  font-size: 0.2408rem;
}
.ranking-header__name {
  background-color: #FFFFFF;
  display: table;
  width: 100%;
  height: 1.036rem;
  line-height: 1.036rem;
  font-size: 0.3192rem;
  border-bottom: 1px solid #DDDDDD;
}
.ranking-header__name_caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: .56px;
  vertical-align: middle;
  border-top: .168rem dashed ;
  border-right: .168rem solid transparent;
  border-left: .168rem solid transparent;
}
.ranking-header__name.active .ranking-header__name_caret {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}
.ranking-header__list {
  position: fixed;
  left: 0;
  right: 0;
  font-size: 0.3192rem;
  z-index: 1;
}
.ranking-header__list_name {
  height: 1.036rem;
  line-height: 1.036rem;
  background-color: #FFFFFF;
  border-bottom: 1px solid #DDDDDD;
  position: relative;
}
.ranking-header__list_img {
  width: .504rem;
  position: absolute;
  top: 0.238rem;
  right: .28rem;
}



