
.font-color-base {
  color: #333333;
}
.font-color-gray {
  color: #7D7D7D;
}
.font-color-red {
  color: #E94543;
}
.font-color-blue {
  color: #42AEF6;
}
.font-color-orange {
  color: #FF820F;
}
.font-color-white {
  color: #FFFFFF;
}
.font-size-base {
  font-size: 0.28rem;
}
.font-size-medium {
  font-size: 0.3192rem;
}
.font-size-large {
  font-size: 0.3388rem;
}
.font-size-larger {
  font-size: 0.3612rem;
}
.font-size-largest {
  font-size: 0.392rem;
}
.font-size-small {
  font-size: 0.308rem;
}
.font-size-smaller {
  font-size: 0.2604rem;
}
.font-size-smallest {
  font-size: 0.2408rem;
}
.font-size-big {
  font-size: 0.448rem;
}
.font-size-bigger {
  font-size: 0.504rem;
}
.font-size-biggest {
  font-size: 0.5992rem;
}
.font-size-very {
  font-size: 0.728rem;
}
.font-size-max {
  font-size: 0.784rem;
}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.info-set {
  margin-top: 0.93rem;
}
.info-set_gray {
  height: .84rem;
  line-height: .84rem;
  padding-left: .3rem;
  font-size: 0.308rem;
}
.info-set_block {
  background-color: #FFFFFF;
  font-size: 0.3192rem;
  border-top: 1px solid #DDDDDD;
  border-bottom: 1px solid #DDDDDD;
}
.info-set_block_item {
  padding-left: .3rem;
}
.info-set_block_item .line {
  border-bottom: 1px solid #DDDDDD;
  height: .98rem;
  line-height: .98rem;
}
.info-set_block_item .line:last-child {
  border-bottom: none;
}
.info-set_block .switch-btn {
  position: relative;
  display: block;
  vertical-align: top;
  width: 1rem;
  height: .56rem;
  border-radius: .56rem;
}
.info-set_block .checked-switch {
  position: absolute;
  top: .03rem;
  left: .08rem;
  opacity: 0;
}
.info-set_block .checked-switch:checked ~ .text-switch {
  background-color: #ed5b49;
  border: 1px solid #d2402e;
}
.info-set_block .checked-switch:checked ~ .toggle-btn {
  left: .39rem;
}
.info-set_block .text-switch {
  background-color: #fff;
  border: 1px solid #DDD;
  border-radius: inherit;
  color: #fff;
  display: block;
  height: inherit;
  position: relative;
  text-transform: uppercase;
}
.info-set_block .toggle-btn {
  background: -webkit-linear-gradient(#fff, #fafafa);
  background: linear-gradient(#fff, #fafafa);
  border-radius: 100%;
  height: .53rem;
  left: 0;
  position: absolute;
  top: .01rem;
  width: .53rem;
  border: 1px solid #DDD;
}
.info-set_block .text-switch,
.info-set_block .toggle-btn {
  transition: All 0.3s ease;
  -webkit-transition: All 0.3s ease;
  -moz-transition: All 0.3s ease;
  -o-transition: All 0.3s ease;
}
.info-set_btn {
  padding-right: .3rem;
  padding-top: 0.2rem;
  float: right;
}



