
.font-color-base {
  color: #333333;
}
.font-color-gray {
  color: #7D7D7D;
}
.font-color-red {
  color: #E94543;
}
.font-color-blue {
  color: #42AEF6;
}
.font-color-orange {
  color: #FF820F;
}
.font-color-white {
  color: #FFFFFF;
}
.font-size-base {
  font-size: 0.28rem;
}
.font-size-medium {
  font-size: 0.3192rem;
}
.font-size-large {
  font-size: 0.3388rem;
}
.font-size-larger {
  font-size: 0.3612rem;
}
.font-size-largest {
  font-size: 0.392rem;
}
.font-size-small {
  font-size: 0.308rem;
}
.font-size-smaller {
  font-size: 0.2604rem;
}
.font-size-smallest {
  font-size: 0.2408rem;
}
.font-size-big {
  font-size: 0.448rem;
}
.font-size-bigger {
  font-size: 0.504rem;
}
.font-size-biggest {
  font-size: 0.5992rem;
}
.font-size-very {
  font-size: 0.728rem;
}
.font-size-max {
  font-size: 0.784rem;
}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.ranking-list {
  margin-top: .28rem;
  border-top: 1px solid #DDDDDD;
  font-size: 0.3192rem;
}
.ranking-list__item {
  background-color: #FFFFFF;
  position: relative;
  z-index: 1;
  padding: .28rem;
}
.ranking-list__item_ranking {
  color: #FFFFFF;
  min-width: .42rem;
  height: .448rem;
  display: inline-block;
  text-align: center;
  border-radius: .056rem;
  padding: 0 .028rem;
}
.ranking-list__item_ranking.red {
  background-color: #E94543 !important;
}
.ranking-list__item_ranking.orange {
  background-color: #FF903F !important;
}
.ranking-list__item_ranking.yellowish {
  background-color: #FFBA29 !important;
}
.ranking-list__item_ranking.gray {
  background-color: #DCDCDC;
}
.ranking-list__item_name {
  display: inline-block;
  margin-left: .112rem;
}
.ranking-list__item_amount {
  position: absolute;
  right: .28rem;
  top: .28rem;
}
.ranking-list__item_shadow {
  position: absolute;
  left: .14rem;
  right: .14rem;
  top: .07rem;
  bottom: .14rem;
  box-shadow: 0 0.056rem 0.14rem 0.056rem #DCDCDC;
}
.ranking-list_background {
  background-color: white;
  position: absolute;
  bottom: 0;
  top: 1.68rem;
  left: 0;
  right: 0;
  z-index: 0;
  max-width: 1080px;
  margin: 0 auto;
}



