}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.product-terms_shadow {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #000000;
  z-index: 1;
  opacity: 0.4;
}
.product-terms__ul {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  z-index: 2;
  background-color: #FFFFFF;
}
.product-terms__ul_item {
  height: 0.9rem;
  line-height: 0.9rem;
  border-bottom: 1px solid #DDDDDD;
  font-size: 0.308rem;
  color: #42AEF6;
}
.product-terms__ul_item:last-child {
  border-bottom: none;
  color: #333333;
}



