
.font-color-base {
  color: #333333;
}
.font-color-gray {
  color: #7D7D7D;
}
.font-color-red {
  color: #E94543;
}
.font-color-blue {
  color: #42AEF6;
}
.font-color-orange {
  color: #FF820F;
}
.font-color-white {
  color: #FFFFFF;
}
.font-size-base {
  font-size: 0.28rem;
}
.font-size-medium {
  font-size: 0.3192rem;
}
.font-size-large {
  font-size: 0.3388rem;
}
.font-size-larger {
  font-size: 0.3612rem;
}
.font-size-largest {
  font-size: 0.392rem;
}
.font-size-small {
  font-size: 0.308rem;
}
.font-size-smaller {
  font-size: 0.2604rem;
}
.font-size-smallest {
  font-size: 0.2408rem;
}
.font-size-big {
  font-size: 0.448rem;
}
.font-size-bigger {
  font-size: 0.504rem;
}
.font-size-biggest {
  font-size: 0.5992rem;
}
.font-size-very {
  font-size: 0.728rem;
}
.font-size-max {
  font-size: 0.784rem;
}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.home-update {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #FFFFFF;
  z-index: 3;
  text-align: center;
  max-width: 1080px;
  margin: 0 auto;
}
.home-update__tip {
  height: 0.65rem;
  line-height: 0.65rem;
}
.home-update__tip_name {
  background-color: #ffecea;
  color: #E94543;
  font-size: 0.308rem;
}
.home-update__icon {
  height: 3.8rem;
}
.home-update__icon_img {
  width: 3.4rem;
}
.home-update__icon_star {
  width: 0.3rem;
  margin: 0 0.02rem;
}
.home-update_give {
  font-size: 0.3192rem;
  margin-top: 0.1rem;
  margin-bottom: 0.2rem;
}
.home-update_linear {
  height: 0.03rem;
  margin-top: 0.4rem;
  width: 6.9rem;
  display: inline-block;
  border-radius: 50%;
  background: -webkit-linear-gradient(left, white, #c5a16e, white);
  background: -webkit-linear-gradient(right, white, #c5a16e, white);
  background: linear-gradient(right, white, #c5a16e, white);
}
.home-update__privilege {
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
  color: #7D7D7D;
}
.home-update__privilege_item {
  display: inline-block;
  font-size: 0.2604rem;
  margin: 0 0.2rem;
}
.home-update__privilege_img {
  width: 0.84rem;
}
.home-update_button {
  height: 0.85rem;
  line-height: 0.85rem;
  width: 5rem;
  background-color: #E94543;
  display: inline-block;
  color: #FFFFFF;
  margin-top: 0.3rem;
  margin-bottom: 0.28rem;
  border-radius: 0.1rem;
  font-size: 0.3192rem;
}



