
.font-color-base {
  color: #333333;
}
.font-color-gray {
  color: #7D7D7D;
}
.font-color-red {
  color: #E94543;
}
.font-color-blue {
  color: #42AEF6;
}
.font-color-orange {
  color: #FF820F;
}
.font-color-white {
  color: #FFFFFF;
}
.font-size-base {
  font-size: 0.28rem;
}
.font-size-medium {
  font-size: 0.3192rem;
}
.font-size-large {
  font-size: 0.3388rem;
}
.font-size-larger {
  font-size: 0.3612rem;
}
.font-size-largest {
  font-size: 0.392rem;
}
.font-size-small {
  font-size: 0.308rem;
}
.font-size-smaller {
  font-size: 0.2604rem;
}
.font-size-smallest {
  font-size: 0.2408rem;
}
.font-size-big {
  font-size: 0.448rem;
}
.font-size-bigger {
  font-size: 0.504rem;
}
.font-size-biggest {
  font-size: 0.5992rem;
}
.font-size-very {
  font-size: 0.728rem;
}
.font-size-max {
  font-size: 0.784rem;
}
.font-bold {
  font-weight: 500;
}
.border-red {
  border: 1px solid #E94543;
}
.border-blue {
  border: 1px solid #42AEF6;
}
.border-gray {
  border: 1px solid #DDDDDD;
}
.common-user-item {
  background-color: #FFFFFF;
  padding: 0.3rem;
}
.common-user-item__wrapper {
  border-bottom: 1px solid #DDDDDD;
}
.common-user-item__view {
  float: right;
  text-decoration: underline;
  color: #42AEF6;
}
.common-user-item__phone {
  font-size: 0.2408rem;
  color: #7D7D7D;
}
.common-user-item__infos {
  margin-top: 0.14rem;
}
.common-user-item__infos::after {
  content: '';
  clear: both;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.common-user-item__info {
  float: left;
  width: 50%;
}
.common-user-item__info:first-child {
  margin-right: -1px;
  border-right: 1px solid #DDDDDD;
}
.common-user-item__info-right {
  padding-left: 0.3rem;
}



